.footer {
  padding: 23px 0 62px;

  @media @tablet {
    padding: 40px 0 13px;
  }

  @media @desktop {
    padding: 120px 16px 11px;
  }
}

.footer__first-row {
  @media @tablet {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 36px repeat(2, 1fr);
    row-gap: 32px;
    margin-bottom: 32px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media @desktop {
    grid-template-rows: repeat(2, 1fr);
    row-gap: 40px;
    margin-bottom: 54px;
  }
}

.footer__title {
  font-family: @text-font;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  color: @second-text-color;
  .margin-tb(42px);
  padding-left: 20px;
  padding-right: 20px;

  @media @tablet {
    font-size: 26px;
    line-height: 32px;
    letter-spacing: 0.04em;
    color: #FCFCFC;
    grid-column: 1/2;
    grid-row: 1/-1;
    .margin-tb(0);
    padding-left: 0;
    padding-right: 0;
  }

  @media @desktop {
    .margin-tb(14px);
    font-weight: 400;
    font-size: 30px;
    line-height: 37px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  }

  & span {
    font-size: 34px;
    line-height: 41px;
    letter-spacing: 0.04em;
    padding-top: 16px;
    display: block;

    @media @tablet {
      font-size: 48px;
      line-height: 59px;
      letter-spacing: 0.06em;
      padding-top: 0;
    }

    @media @desktop {
      font-weight: 700;
      font-size: 58px;
      line-height: 71px;
    }
  }
}

.footer__social {
  .list();
  margin-bottom: 38px;
  margin-right: 20px;

  @media @tablet {
    grid-column: 2/3;
    grid-row: 2/3;
    align-self: end;
    margin: 0;

    @media @desktop {
      grid-row: 1/2;
    }
  }
}

.footer__contacts {
  .margin-tb(40px);
  padding-right: 20px;
  font-family: @text-font;
  font-weight: 700;
  font-size: 12px;
  line-height: 22px;
  text-align: right;
  color: @text-color;

  @media @tablet {
    font-size: 14px;
    line-height: 26px;
    grid-column: 2/3;
    grid-row: 3/4;
    .margin-tb(0px);
  }

  @media @desktop {
    font-size: 16px;
    line-height: 30px;
    letter-spacing: 0.02em;
    grid-row: 2/3;
    padding-right: 0;
  }
}

.footer__tel {
  .links();
  color: @second-text-color;
}

.footer__copyright {
  .margin-tb(0);
  padding-top: 16px;
  padding-left: 20px;
  padding-right: 20px;
  border-top: 1px solid #E1E1E1;
  font-family: @text-font;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: #7A7A7A;

  @media @tablet {
    line-height: 26px;
  }

  @media @desktop {
    padding-top: 12px;
    font-size: 12px;
    line-height: 22px;
    letter-spacing: 0.02em;
  }

  & span {
    display: block;

    @media @tablet {
      display: inline;
    }
  }
}
