.header {
  padding: 12px 20px 12px 22px;
  position: relative;

  @media @tablet {
    padding: 20px 32px 38px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 32px;
    z-index: 10;
  }

  @media @desktop {
    padding: 16px;
    align-items: center;
  }
}

.header__logo {
  position: relative;
  z-index: 6;

  @media @tablet {
    margin-right: auto;
  }

  @media @desktop {
    margin: 0;
  }
}
