.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 27px;

  @media @tablet {
    gap: 30px;
  }
}

.social__link {
  .links();
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  background: @button-bg;
  border-radius: 50%;

  @media @tablet {
    width: 34px;
    height: 34px;
  }
}

.social__icon {
  fill: @main-bg;
}

.social__link:hover .social__icon,
.social__link:focus .social__icon {
  fill: #fff;
}
