.form__field {
  font-family: @text-font;
  font-weight: 400;
  font-size: 12px;
  line-height: 17px;
  color: @text-color;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid #84A178;
  border-radius: 10px;
  display: block;
  padding: 8px 12px 7px;
  width: 100%;
  margin-bottom: 12px;

  @media @tablet {
    margin-bottom: 16px;
    padding: 11px 16px;
    font-size: 14px;
    line-height: 20px;
  }

  &--area {
    height: 64px;
    margin-bottom: 24px;

    @media @tablet {
      height: 84px;
      margin-bottom: 32px;
    }
  }

  &::placeholder {
    color: @text-color;
    font-family: inherit;
    font-size: inherit;
  }
}
