.products {
  background-color: @second-bg;
  background-image: url(../images/bg/products-bg.jpg);
  background-repeat: repeat-y;
  background-position: center top;
  background-size: 100% 100%;
  padding-top: 60px;
  padding-bottom: 60px;

  @media (min-resolution: 144dpi) {
    background-image: url(../images/bg/products-bg@2x.jpg);
  }

  @media (min-width: 380px) {
    background-image: url(../images/bg/products-bg-tablet.jpg);
    background-size: auto;

    @media (min-resolution: 144dpi) {
      background-image: url(../images/bg/products-bg-tablet@2x.jpg);
    }
  }

  @media @tablet {
    background-size: 100% 100%;
    padding-bottom: 82px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media (min-width: 840px) {
    background-image: url(../images/bg/products-bg-desktop.jpg);
    background-size: auto;

    @media (min-resolution: 144dpi) {
      background-image: url(../images/bg/products-bg-desktop@2x.jpg);
    }
  }

  @media @desktop {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 66px;
  }
}

.products__title {
  font-family: @title-font;
  font-weight: 400;
  font-size: 30px;
  line-height: 38px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: @text-color;
  .margin-tb(40px);

  @media @tablet {
    font-size: 34px;
    line-height: 43px;
    .margin-tb(80px);
  }

  @media @desktop {
    font-size: 36px;
    line-height: 45px;
    letter-spacing: 0.06em;
    .margin-tb(120px);
  }

  & span {
    font-family: @text-font;
    font-weight: 800;
    font-size: 18px;
    line-height: 22px;
    .margin-tb(12px);
    display: block;

    @media @tablet {
      font-size: 22px;
      line-height: 27px;
    }

    @media @desktop {
      font-size: 30px;
      line-height: 37px;
      letter-spacing: 0.04em;
    }
  }
}

.products__list {
  .list();

  @media @desktop {
    margin-left: auto;
    margin-right: auto;
    width: 1175px;
  }
}
