.address {
  font-family: @text-font;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  letter-spacing: 0.04em;
  padding-bottom: 24px;
  border-bottom: 1px solid #E1E1E1;

  @media @tablet {
    font-size: 14px;
    line-height: 26px;
    letter-spacing: 0.04em;
  }

  @media @desktop {
    font-size: 16px;
    line-height: 30px;
    padding-bottom: 32px;
  }
}

.address__town {
  color: @text-color;
  display: block;
  margin-bottom: 8px;

  @media @tablet {
    margin-bottom: 12px;
  }

  @media @desktop {
    margin-bottom: 8px;
  }
}

.address__street {
  color: #D6936D;
  display: block;
  text-decoration: none;

  @media @desktop {
    line-height: 29px;
  }
  &:hover, &:focus {
    text-decoration: underline;
  }
}
