.secondary-btn {
  .links();
  display: block;
  min-width: fit-content;
  border: 2px solid @button-bg;
  border-radius: 8px;
  font-family: @text-font;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: @text-color;
  padding: 12px 34px;
  transition: color .3s, border-color .3s;

  &:hover {
    border: 2px solid @button-second-bg;
    color: @text-color;
  }

  &:focus {
    border: 2px solid @button-second-bg;
    color: @second-text-color;
  }

  &:disabled,
  &--disabled {
    border: 2px solid @disabled;
    color: @text-color;

    &:hover,
    &:focus {
      border: 2px solid @disabled;
      color: @text-color;
    }
  }

  &--small {
    display: inline-block;
    font-size: 8px;
    line-height: 10px;
    padding: 10px;

    &:hover,
    &:disabled,
    &--disabled {
      color: #000;
    }

    @media @tablet {
      padding-top: 7px;
      padding-bottom: 7px;
    }

    @media @desktop {
      font-size: 14px;
      line-height: 18px;
      padding: 12px 34px;
    }
  }
}

.secondary-btn__svg {
  stroke: @text-color;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  transition: stroke .3s;
}

.secondary-btn:focus .secondary-btn__svg {
  stroke: #fff;
}

.secondary-btn:disabled:focus .secondary-btn__svg,
.secondary-btn--disabled:focus .secondary-btn__svg {
  stroke: @text-color;
}
