.how {
  background: @main-bg;
  padding: 60px 20px 36px;

  @media @tablet {
    padding: 60px 32px 40px;
  }

  @media @desktop {
    padding: 60px 16px 86px;
  }
}

.how_subtitle {
  .margin-tb(12px);
  font-family: @text-font;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: @second-text-color;

  @media @tablet {
    font-weight: 800;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.04em;
  }

  @media @desktop {
    font-size: 30px;
    line-height: 37px;
  }
}

.how__title {
  .margin-tb(32px);
  font-family: @title-font;
  font-weight: 400;
  font-size: 30px;
  line-height: 38px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: @second-text-color;

  @media @tablet {
    .margin-tb(72px);
    font-size: 34px;
    line-height: 43px;
    letter-spacing: 0.06em;
  }

  @media @desktop {
    .margin-tb(82px);
    font-size: 36px;
    line-height: 45px;
  }
}

.how__columns {
  @media @tablet {
    display: flex;
    gap: 32px;
    margin-bottom: 48px;
  }

  @media @desktop {
    max-width: 1168px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 60px;
    gap: 100px;
  }
}

.how__img-wrapper {
  width: 279px;
  height: 300px;
  margin-bottom: 40px;
  margin-right: auto;
  margin-left: auto;

  @media @desktop {
    width: 500px;
    height: 529px;
  }
}

.how__text-wrapper {
  @media @desktop {
    max-width: 465px;
  }
}

.how__text {
  .margin-tb(0);
  font-family: @text-font;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.04em;
  color: @second-text-color;

  @media @tablet {
    .margin-tb(8px);
    text-align: left;
  }

  @media @desktop {
    font-size: 16px;
    line-height: 28px;
  }

  &--not-mobile {
    display: none;

    @media @tablet {
      display: block;
    }
  }
}

.how__more-link {
  width: 168px;
  margin: 20px auto 42px;

  @media @tablet {
    margin: 48px auto 0;
  }
}

.how__list {
  .list();

  @media @desktop {
    max-width: 1168px;
    margin-left: auto;
    margin-right: auto;
  }
}
