.feedback {
  padding: 0;
  margin: 0;
}

.feedback__img {
  display: block;
  width: 85px;
  height: 85px;
  margin: 0 auto 32px;
  border-radius: 50%;

  @media @tablet {
    margin-bottom: 28px;
  }

  @media @desktop {
    margin-bottom: 32px;
  }
}

.feedback__text {
  .margin-tb(30px);
  font-family: @text-font;
  font-weight: 400;
  font-size: 12px;
  line-height: 31px;
  text-align: center;
  color: @second-text-color;
  padding: 34px 39px;
  border-radius: 10px;
  background: @main-bg;
  min-height: 257px;
  display: flex;
  align-items: center;

  @media @tablet {
    font-weight: 500;
    font-size: 14px;
    line-height: 26px;
    letter-spacing: 0.04em;
    padding: 29px 80px 28px;
    min-height: 140px;
    .margin-tb(32px);
  }

  @media @desktop {
    font-size: 16px;
    line-height: 30px;
    padding: 39px 74px 39px 73px;
    min-height: auto;
  }
}

.feedback__author {
  font-family: @text-font;
  font-weight: 500;
  font-size: 20px;
  font-style: normal;
  line-height: 37px;
  text-align: center;
  color: @text-color;
  display: block;
  text-align: center;

  @media @tablet {
    font-size: 16px;
    line-height: 30px;
  }
}
