*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  background: #84A178;
  color: #ffffff;
  font-style: normal;
  margin: 0;
  display: flex;
  flex-direction: column;
  max-height: 100%;
}
.page__main {
  flex-grow: 1;
}
.button {
  text-decoration: none;
  color: inherit;
  display: block;
  padding: 12px 34px;
  border-radius: 10px;
  border: none;
  background: #F8D24E;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #131313;
  transition: color 0.3s, background-color 0.3s;
}
.button:hover {
  color: #000000;
  background: #0A8817;
}
.button:focus {
  color: #FCFCFC;
  background: #0A8817;
}
.button:disabled,
.button--disabled {
  color: #000000;
  background: #ababab;
}
.button:disabled:hover,
.button--disabled:hover,
.button:disabled:focus,
.button--disabled:focus {
  color: #000000;
  background: #ababab;
}
.button__svg {
  stroke: #131313;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  transition: stroke 0.3s;
}
@media (min-width: 1201px) {
  .button__svg {
    margin-left: 6px;
  }
}
.button:focus .button__svg {
  stroke: #fff;
}
.button:disabled:focus .button__svg,
.button--disabled:focus .button__svg {
  stroke: #131313;
}
.secondary-btn {
  text-decoration: none;
  color: inherit;
  display: block;
  min-width: fit-content;
  border: 2px solid #F8D24E;
  border-radius: 8px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #131313;
  padding: 12px 34px;
  transition: color 0.3s, border-color 0.3s;
}
.secondary-btn:hover {
  border: 2px solid #0A8817;
  color: #131313;
}
.secondary-btn:focus {
  border: 2px solid #0A8817;
  color: #FCFCFC;
}
.secondary-btn:disabled,
.secondary-btn--disabled {
  border: 2px solid #ababab;
  color: #131313;
}
.secondary-btn:disabled:hover,
.secondary-btn--disabled:hover,
.secondary-btn:disabled:focus,
.secondary-btn--disabled:focus {
  border: 2px solid #ababab;
  color: #131313;
}
.secondary-btn--small {
  display: inline-block;
  font-size: 8px;
  line-height: 10px;
  padding: 10px;
}
.secondary-btn--small:hover,
.secondary-btn--small:disabled,
.secondary-btn--small--disabled {
  color: #000;
}
@media (min-width: 768px) {
  .secondary-btn--small {
    padding-top: 7px;
    padding-bottom: 7px;
  }
}
@media (min-width: 1201px) {
  .secondary-btn--small {
    font-size: 14px;
    line-height: 18px;
    padding: 12px 34px;
  }
}
.secondary-btn__svg {
  stroke: #131313;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  transition: stroke 0.3s;
}
.secondary-btn:focus .secondary-btn__svg {
  stroke: #fff;
}
.secondary-btn:disabled:focus .secondary-btn__svg,
.secondary-btn--disabled:focus .secondary-btn__svg {
  stroke: #131313;
}
