.navigation__burger-btn {
  border: none;
  background: transparent;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 16px;
  right: 20px;
  z-index: 5;
  padding: 6px;
  cursor: pointer;
}
.navigation__burger-btn--nojs {
  display: none;
}
@media (min-width: 768px) {
  .navigation__burger-btn {
    position: static;
  }
}
@media (min-width: 1201px) {
  .navigation__burger-btn {
    display: none;
  }
}
.navigation__burger-btn span {
  display: block;
  width: 20px;
  height: 2px;
  background: #fff;
  opacity: 1;
  transition: transform 0.3s, opacity 0.3s;
}
.navigation__burger-btn span:first-child {
  transform: translateY(0);
}
.navigation__burger-btn span:nth-child(2) {
  transform: translateY(4px);
  height: 1px;
}
.navigation__burger-btn span:nth-child(3) {
  transform: translateY(4px);
  height: 1px;
}
.navigation__burger-btn span:nth-child(4) {
  transform: translateY(8px);
}
.navigation__burger-btn--open {
  position: absolute;
}
.navigation__burger-btn--open span:first-child {
  transform: translateY(8px) translateX(2px) rotate(45deg);
}
.navigation__burger-btn--open span:nth-child(2),
.navigation__burger-btn--open span:nth-child(3) {
  opacity: 0;
}
.navigation__burger-btn--open span:nth-child(4) {
  transform: translateY(4px) translateX(2px) rotate(-45deg);
}
