.card {
  .links();
  display: block;
  border-radius: 24px;
  padding: 123px 40px 20px;

  @media @tablet {
    padding-left: 12px;
    padding-right: 12px;
    padding-top: 108px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  @media @desktop {
    padding: 222px 62px 60px;
  }

  &--icecream {
    background-color: #AEC6A5;
  }

  &--icecoffee {
    background-color: #EFD478;
  }

  &--milkshakes {
    background-color: #D6936D;
  }
}

.card__title {
  font-family: @text-font;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: @text-color;
  .margin-tb(40px);

  @media @tablet {
    font-size: 22px;
    line-height: 27px;
  }

  @media @desktop {
    font-size: 30px;
    line-height: 37px;
    .margin-tb(65px);
  }
}

.card__descr {
  font-family: @text-font;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  text-align: center;
  letter-spacing: 0.04em;
  color: @text-color;
  .margin-tb(90px);

  @media @tablet {
    .margin-tb(60px);
  }

  @media @desktop {
    font-size: 16px;
    line-height: 28px;
  }
}

.card__arrow {
  fill: @text-color;
  display: block;
  margin: 0 auto;
  transform-origin: left center;
}

.card:hover .card__arrow {
  transform: scaleX(1.25);
}

.card:focus .card__arrow {
  transform: scale(1.35);
}
