.products-list {
  @media @tablet {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 16px;
  }

  @media @desktop {
    gap: 32px;
  }
}

.products-list__item {
  display: flex;
  position: relative;
}

.products-list__link {
  width: 280px;
  margin: 108px auto 56px;

  @media @tablet {
    width: 224px;
    margin-top: 120px;
    margin-bottom: 0;
  }

  @media @desktop {
    width: 370px;
  }
}

.products-list__img {
  position: absolute;
  width: 200px;
  height: 210px;
  left: calc(50% - 100px);

  @media @tablet {
    position: absolute;
    left: 12px;
  }

  @media @desktop {
    width: 302px;
    height: 317px;
    left: calc(50% - 151px);
  }
}
