.food-values {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-family: @text-font;
  font-size: 10px;
  color: @text-color;

  @media @tablet {
    justify-content: center;
    gap: 20px;
    font-size: 12px;
  }
}

.food-values__item {
  width: 49px;
  border: 2px solid #9CBE8F;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  @media @tablet {
    width: 65px;
    padding-bottom: 14px;
  }
}

.food-values__digit {
  font-weight: 500;
  line-height: 28px;
  width: 43px;
  height: 43px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #9CBE8F;
  border: 2px solid #9CBE8F;
  border-radius: 50%;
  box-shadow: 0 6px 20px rgba(10, 136, 23, 0.25);
  margin-bottom: 8px;

  @media @tablet {
    width: 53px;
    height: 53px;
  }
}

.food-values__name {
  margin: 0;
  font-weight: 400;
  line-height: 12px;

  @media @tablet {
    line-height: 15px;
  }
}
