.order-card {
  padding: 0 0 20px;
  border-radius: 16px;

  &--icecream {
    border: 2px solid #96AF8C;
    filter: drop-shadow(0px 5px 20px rgba(212, 20, 67, 0.1));
  }

  &--icecoffee {
    background-color: #EFD478;
    border: 2px solid #EFD478;
    box-shadow: 0px 5px 20px rgba(212, 20, 67, 0.1);
  }

  &--milkshakes {
    border: 2px solid #D6936D;
    filter: drop-shadow(0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
  }
}

.order-card__img-wrapper {
  width: 132px;
  height: 139px;
  margin-top: -50%;
  margin-bottom: 20px;
}

.order-card__title {
  font-family: @text-font;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: @text-color;
  padding: 0 2px;
  .margin-tb(0);
}
