.facts {
  @media @tablet {
    display: flex;
    justify-content: space-between;
    padding-left: 44px;
    padding-right: 44px;
  }

  @media @desktop {
    padding-left: 0;
    padding-right: 0;
    gap: 32px;
  }
}

.facts__item {
  @media @desktop {
    max-width: 368px;
  }

  &::before {
    content: "";
    display: block;
    width: 50px;
    height: 50px;
    margin: 0 auto 16px;
    border-radius: 50%;
    background-color: @button-bg;
    background-repeat: no-repeat;
    background-position: center;

    @media @tablet {
      margin-bottom: 32px;
    }

    @media @desktop {
      margin-bottom: 40px;
    }
  }

  &--cow::before {
    background-image: url(../images/icon-cow.svg);
  }

  &--kg::before {
    background-image: url(../images/icon-weight.svg);
  }

  &--kinds::before {
    background-image: url(../images/icon-icecream.svg);
  }
}

.facts__digit {
  font-family: @text-second-font;
  font-weight: 700;
  font-size: 36px;
  line-height: 42px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: @second-text-color;
  display: block;
  margin-bottom: 18px;

  @media @tablet {
    margin-bottom: 16px;
    font-family: @text-font;
    font-size: 48px;
    line-height: 59px;
    text-transform: none;
  }

  @media @desktop {
    font-size: 58px;
    line-height: 71px;
  }
}

.facts__text {
  font-family: @text-font;
  font-weight: 700;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  color: @second-text-color;
  display: block;
  margin-bottom: 28px;

  @media @tablet {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 0;
  }

  @media @desktop {
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.02em;
  }
}
