.hero {
  background: url(../images/bg/dots1.svg) no-repeat left bottom, url(../images/bg/dots2.svg) no-repeat right bottom @main-bg;
  padding: 61px 20px 0;
  position: relative;

  @media @tablet {
    padding: 6px 32px 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    background: url(../images/bg/dots1-tablet.svg) no-repeat left bottom, url(../images/bg/dots2-tablet.svg) no-repeat right bottom @main-bg;
  }

  @media @desktop {
    padding: 29px 16px 0 17px;
    background: url(../images/bg/dots1-desktop.svg) no-repeat left bottom 17px, url(../images/bg/dots2-desktop.svg) no-repeat right bottom, url(../images/bg/dots3-desktop.svg) no-repeat left 235px bottom 45px @main-bg;
    grid-template-rows: auto 290px;
  }
}

.hero__title-group {
  @media @desktop {
    margin-bottom: 42px;
  }
}

.hero__title {
  .margin-tb(32px);
  font-family: @title-font;
  font-weight: 400;
  font-size: 26px;
  line-height: 33px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: @second-text-color;

  @media @tablet {
    .margin-tb(21px);
    font-size: 18px;
    line-height: 23px;
    max-width: 185px;
  }

  @media @desktop {
    font-size: 38px;
    line-height: 48px;
    .margin-tb(28px);
    max-width: 370px;
  }

  & span {
    @media @tablet {
      font-size: 24px;
      line-height: 13px;
    }

    @media @desktop {
      font-size: 38px;
      line-height: 48px;
    }
  }
}

.hero__btn {
  margin-right: 8px;

  @media @desktop {
    margin-right: 34px;
  }
}

.hero__link {
  .links();
  display: inline-block;
  padding: 11px 10px;
  font-family: @text-font;
  font-weight: 600;
  font-size: 8px;
  line-height: 10px;
  text-align: center;
  text-decoration: underline;
  color: @button-bg;
  transition: color .3s;

  @media @desktop {
    font-size: 14px;
    line-height: 17px;
  }

  &:hover {
    color: @button-second-bg;
  }

  &:focus {
    color: @second-text-color;
  }

  &:disabled,
  &--disabled {
    color: @disabled;

    &:hover,
    &:focus {
      color: @disabled;
    }
  }
}

.hero__img {
  display: block;

  @media @tablet {}
}

.hero__img-wrapper {
  width: 276px;
  height: 274px;
  margin: -40px auto 0;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-left: 27px;

  @media @tablet {
    width: 347px;
    height: 398px;
    padding-left: 0;
    margin: -66px 0 0;
    overflow: visible;
    padding-left: 61px;
  }

  @media @desktop {
    padding-left: 0;
    margin-top: -114px;
    width: 484px;
    height: 590px;
  }
}

.hero__descr-group {
  display: none;

  @media @tablet {
    display: flex;
    column-gap: 33px;
    grid-column: 1/2;
    grid-row: 2/3;
    padding-bottom: 14px;
  }

  @media @desktop {
    align-self: start;
    column-gap: 14px;
    align-items: center;
    padding-bottom: 60px;
  }
}

.hero__descr-img {
  display: none;

  @media @tablet {
    display: block;
    width: 152px;
    height: 152px;
  }

  @media @desktop {
    width: 225px;
    height: 225px;
  }
}

.hero__descr-text {
  display: none;

  @media @tablet {
    .margin-tb(0);
    display: block;
    font-family: @text-font;
    font-weight: 400;
    font-size: 12px;
    line-height: 17px;
    color: @second-text-color;
  }

  @media @desktop {
    font-size: 14px;
    line-height: 20px;
    max-width: 200px;
  }
}

.hero__arrow {
  display: none;

  @media @tablet {
    display: block;
    fill: @second-text-color;
    margin-top: 20px;
  }

  @media @desktop {
    margin-top: 32px;
  }
}

.hero__facts {
  display: none;

  @media @tablet {
    display: block;
    .list();
  }
}

.hero__facts-digit {
  font-family: @text-font;
  font-weight: 900;
  font-size: 28px;
  line-height: 34px;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: @text-color;

  @media @desktop {
    font-size: 46px;
    line-height: 56px;
  }
}

.hero__facts-text {
  margin: 0 0 20px;
  font-family: @text-font;
  font-weight: 400;
  font-size: 8px;
  line-height: 10px;
  text-align: right;
  color: @second-text-color;

  @media @desktop {
    font-size: 14px;
    line-height: 17px;
  }
}

.hero__visual {
  @media @tablet {
    grid-column: 2/-1;
    grid-row: 1/-1;
    display: flex;
  }

  @media @desktop {
    justify-self: end;
  }
}
