.cafe-card {
  background: @second-bg;
  border-radius: 24px;
  padding: 40px 32px;

  @media @tablet {
    padding: 40px 24px;
  }

  @media @desktop {
    padding: 40px;
  }
}

.cafe-card__type {
  .margin-tb(20px);
  font-family: @text-font;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: @text-color;
  width: max-content;
  background: @button-bg;
  border-radius: 4px;
  padding-right: 24px;
  padding-left: 24px;

  @media @tablet {
    .margin-tb(32px);
    font-size: 16px;
    line-height: 30px;
    padding-right: 15px;
    padding-left: 15px;
  }

  @media @desktop {
    padding: 0 13px;
  }
}

.cafe-card__post-address {
  margin-bottom: 24px;

  @media @tablet {
    margin-bottom: 26px;
  }

  @media @desktop {
    margin-bottom: 32px;
  }
}

.cafe-card__worktime {
  .list();
  margin-bottom: 16px;

  @media @tablet {
    margin-bottom: 32px;
  }
}
