.popup {
  width: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 50;
  background: rgba(@main-bg, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;

  &--nojs {
    position: relative;
    align-items: flex-start;
  }

  &--hidden {
    display: none;
  }
}

.popup__modal {
  position: absolute;
  width: 280px;
  margin: auto;
  padding: 28px 29px 28px 30px;
  background: @second-text-color;
  border-radius: 10px;
  border: 2px solid @button-bg;
  overflow-y: auto;
  height: 100vh;

  @media @tablet {
    padding: 32px 54px;
    width: 568px;
    height: 100%;
  }

  &--ingredients {
    padding-left: 20px;
    padding-right: 20px;

    @media @tablet {
      padding-right: 40px;
      padding-left: 40px;
    }
  }

  &--short-content {
    height: auto;
  }
}

.popup--nojs .popup__modal {
  position: relative;
  margin-bottom: 24px;
}

.popup__title {
  font-family: @title-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: @text-color;
  .margin-tb(20px);

  &--ingredients {
    font-size: 20px;
    line-height: 28px;
  }

  @media @tablet {
    font-size: 20px;
    line-height: 28px;
    .margin-tb(40px);
  }

  &--mb32 {
    @media @tablet {
      margin-bottom: 32px;
    }
  }

  &--mb24 {
    @media @tablet {
      margin-bottom: 24px;
    }
  }
}

.popup__image {
  width: 224px;
  height: 199px;
  display: flex;
  margin: 0 auto 24px;
  border-radius: 10px;

  @media @tablet {
    width: 448px;
    height: 260px;
    margin-bottom: 32px;
  }
}

.popup__video-wrapper {
  position: relative;
  margin-bottom: 20px;

  @media @tablet {
    margin-bottom: 32px;
  }
}

.popup__video {
  display: block;
  width: 224px;
  height: 199px;
  margin: 0 auto;
  background: #000;
  border-radius: 10px;

  @media @tablet {
    width: 448px;
    height: 205px;
  }
}

.popup__video-play {
  background: transparent;
  border: none;
  position: absolute;
  top: calc(50% - 18px);
  left: calc(50% - 18px);

  & svg {
    fill: #fff;
  }
}

.popup__text {
  font-family: @text-font;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: @text-color;
  .margin-tb(0);

  @media @tablet {
    font-size: 16px;
    line-height: 22px;
  }
}

.popup__btn {
  margin: 0 auto;
  max-width: fit-content;
}

.popup__close {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 16px;
  height: 16px;

  @media @tablet {
    width: 20px;
    height: 20px;
    top: 24px;
    right: 24px;
  }

  &--ingredients {
    top: 28px;
    right: 20px;
    width: 20px;
    height: 20px;

    @media @tablet {
      top: 20px;
    }
  }
}

.popup__form {
  margin: 24px auto 0;
  width: 224px;

  @media @tablet {
    margin-top: 40px;
    width: 368px;
  }

  &--mt32 {
    @media @tablet {
      margin-top: 32px;
    }
  }
}

.popup__order-list {
  .list();
}

.popup__food-values {
  .list();
  margin-bottom: 39px;
}

.popup__medalion {
  width: 132px;
  height: 138px;
  margin: 0 auto 24px;
}

.popup__table-title {
  font-family: @text-font;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  text-align: center;
  color: @text-color;
  .margin-tb(24px);
}

.popup__ingredients-table {
  margin-bottom: 48px;
}
