.navigation {

  @media @tablet {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    column-gap: 32px;
  }

  @media @desktop {
    padding-top: 5px;
  }
}

.navigation__burger-btn {
  border: none;
  background: transparent;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 16px;
  right: 20px;
  z-index: 5;
  padding: 6px;
  cursor: pointer;

  &--nojs {
    display: none;
  }

  @media @tablet {
    position: static;
  }

  @media @desktop {
    display: none;
  }
}

.navigation__burger-btn span {
  display: block;
  width: 20px;
  height: 2px;
  background: #fff;
  opacity: 1;
  transition: transform .3s, opacity .3s;

  &:first-child {
    transform: translateY(0);
  }

  &:nth-child(2) {
    transform: translateY(4px);
    height: 1px;
  }

  &:nth-child(3) {
    transform: translateY(4px);
    height: 1px;
  }

  &:nth-child(4) {
    transform: translateY(8px);
  }
}

.navigation__burger-btn--open {
  position: absolute;

  & span:first-child {
    transform: translateY(8px) translateX(2px) rotate(45deg);
  }

  & span:nth-child(2),
  & span:nth-child(3) {
    opacity: 0;
  }

  & span:nth-child(4) {
    transform: translateY(4px) translateX(2px) rotate(-45deg);
  }
}

.navigation__list {
  display: none;

  @media @desktop {
    display: flex;
    gap: 27px;
  }

  &--nojs {
    display: block;

    @media @tablet {
      min-height: 100vh;
    }
  }

  &--open {
    display: block;

    @media @tablet {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 3;
      min-height: 1024px;
    }
  }

}

.navigation__btn {
  max-width: 152px;
  margin: 0 auto;

  @media @tablet {
    display: none;
  }

  &--outnav {
    display: none;

    @media @tablet {
      display: block;
      margin: 2px 0 0;
    }

    @media @desktop {
      margin-top: 10px;
    }
  }
}
