.contacts {
  font-family: @text-font;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: @text-color;

  @media @tablet {
    font-size: 14px;
    line-height: 26px;
  }

  @media @desktop {
    font-size: 16px;
    line-height: 30px;
  }
}

.contacts__tel {
  display: block;
  .links();
}

.contacts__mail {
  display: block;
  .links();
}
