.main-nav {
  .list();
  padding-left: 11px;
  padding-right: 11px;
  padding-top: 36px;
  padding-bottom: 63px;

  @media @tablet {
    background: #97AF8D;
    padding-top: 140px;
  }

  @media @desktop {
    background: transparent;
    padding: 0;
  }
}

.main-nav__link {
  .links();
  display: block;
  font-weight: 700;
  font-family: @text-font;
  font-size: 32px;
  line-height: 39px;
  text-align: center;
  color: @second-text-color;
  margin-bottom: 32px;

  @media @desktop {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 0;
  }

  &:hover,
  &:focus {
    color: @text-color;

    @media @desktop {
      color: @button-bg;
    }
  }
}
