.button {
  .links();
  display: block;
  padding: 12px 34px;
  border-radius: 10px;
  border: none;
  background: @button-bg;
  font-family: @text-font;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: @text-color;
  transition: color .3s, background-color .3s;

  &:hover {
    color: #000000;
    background: @button-second-bg;
  }

  &:focus {
    color: @second-text-color;
    background: @button-second-bg;
  }

  &:disabled,
  &--disabled {
    color: #000000;
    background: @disabled;

    &:hover,
    &:focus {
      color: #000000;
      background: @disabled;
    }
  }
}

.button__svg {
  stroke: @text-color;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  transition: stroke .3s;

  @media @desktop {
    margin-left: 6px;
  }
}

.button:focus .button__svg {
  stroke: #fff;
}

.button:disabled:focus .button__svg,
.button--disabled:focus .button__svg {
  stroke: @text-color;
}
