.worktime {
  font-family: @text-font;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  padding-bottom: 24px;
  border-bottom: 1px solid #E1E1E1;

  @media @tablet {
    font-size: 14px;
    line-height: 26px;
    padding-bottom: 24px;
  }

  @media @desktop {
    padding-bottom: 20px;
    font-size: 16px;
    line-height: 30px;
  }
}

.worktime__weekdays {
  color: @text-color;

  @media @desktop {
    letter-spacing: 0.04em;
  }
}

.worktime__hours {
  color: #ABABAB;
  margin-left: 0;
  margin-bottom: 8px;

  @media @tablet {
    color: #907E82;
    margin-bottom: 12px;
  }
}
