.for-clients {
  background-image: url(../images/bg/for-clients-bg.jpg);
  background-repeat: no-repeat;
  background-position: center 108px;
  background-color: @main-bg;
  background-size: 100% 940px;

  @media (min-resolution: 144dpi) {
    background-image: url(../images/bg/for-clients-bg@2x.jpg);
  }

  @media (min-width: 380px) {
    background-image: url(../images/bg/for-clients-bg-tablet.jpg);
    background-size: auto 940px;

    @media (min-resolution: 144dpi) {
      background-image: url(../images/bg/for-clients-bg-tablet@2x.jpg);
    }
  }

  @media (min-width: 750px) {
    background-size: 768px auto;
  }

  @media @tablet {
    background-position: center 165px;
    background-size: 100% auto;
    padding-left: 31px;
    padding-right: 32px;
  }

  @media (min-width: 880px) {
    background-image: url(../images/bg/for-clients-bg-desktop.jpg);
    background-size: 100% auto;

    @media (min-resolution: 144dpi) {
      background-image: url(../images/bg/for-clients-bg-desktop@2x.jpg);
    }
  }

  @media @desktop {
    background-position: center 98px;
    background-size: auto;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.for-clients__header {
  background: linear-gradient(to bottom, @main-bg 25px, @second-bg 25px);
  margin-bottom: 80px;

  @media @tablet {
    background: linear-gradient(to bottom, @main-bg 164px, @second-bg 164px);
    margin-bottom: 60px;
  }

  @media (min-width: 880px) {
    background: none;
  }

  @media @desktop {
    background: none;
    margin-bottom: 128px;
  }
}

.for-clients__img {
  display: block;
  width: 279px;
  height: 130px;
  border-radius: 7px;
  margin: 0 auto;

  @media @tablet {
    height: 327px;
    width: 704px;
    border-radius: 18px;
  }

  @media @desktop {
    height: 427px;
    width: 1013px;
    border-radius: 24px;
  }
}

.for-clients__slider {
  margin-bottom: 48px;

  @media @tablet {
    margin-bottom: 32px;
  }

  @media @desktop {
    margin-bottom: 100px;
    width: 968px;
    margin-left: auto;
    margin-right: auto;
  }
}

.for-clients__contacts {
  .list();
  margin-bottom: 20px;

  @media @tablet {
    display: flex;
    gap: 16px;
    margin-bottom: 60px;
  }

  @media @desktop {
    max-width: 1168px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 64px;
    gap: 32px;
  }
}

.for-clients__location {
  width: 280px;
  margin: 0 auto 20px;

  @media @tablet {
    margin-bottom: 0;
  }

  @media @desktop {
    width: 368px;
    margin: 0;
  }
}

.for-clients__btn-wrapper {
  @media @tablet {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 16px;
  }
}

.for-clients__button {
  width: 196px;
  margin: 0 auto 16px;

  @media @tablet {
    margin: 0;
  }
}
